.App {
  text-align: center;
  padding: 2% 5%;
}

.App * {
  box-sizing: border-box;
}

ul {
  padding: 0;
  list-style: none;
}

p {
  margin: 5px 0;
}

input[type="text"], input[type="number"] {
  padding: 8px 6px 8px 10px;
  border-radius: 4px;
  outline: none;
  border: 1px solid #ccc;
  font-size: 14px;
  line-height: 20px;
  align-self: center;
  height: 38px;
}

input[type="text"]:hover,
input[type="number"]:hover,
input[type="text"]:focus,
input[type="number"]:focus {
  border: 3px solid #ccc;
}

.city {
  width: 250px;
}

.CitySearch {
  display: flex;
  flex-direction: column;
}

.suggestions {
  width: 250px;
  align-self: center;
  margin: 0;
  font-size: 14px;
}

.suggestions > li {
  text-align: left;
  padding: 8px 6px 8px 10px;
  background-color: #eee;
  cursor: default;
}

.suggestions > li:last-child {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}


.suggestions > li:hover {
  background-color: #BCDCDB;
}

.EventList {
 column-count: 1;
}

li{
  break-inside: avoid;
}

.event {
  padding: 10px 10px 20px;
  margin-bottom: 10px;
  text-align: left;
  border: 1px solid #ccc;
  border-radius: 4px;
  position: relative;
}

.eventHead {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.detailsButton {
  position: absolute;
  right: 5px;
  bottom: 5px;
  padding: 5px 8px;
  border-radius: 4px;
  outline: none;
  cursor: pointer;
  font-size: 14px;
}

.eventTitle {
  font-size: 18px;
  font-weight: 600;
}

.eventGroup {
  text-transform: uppercase;
  font-size: 14px;
  padding-bottom: 1rem;
}

.eventFooter {
  padding-top: 1rem;
  display: flex;
  justify-content: space-between;
}

.eventPlace {
  text-align: left;
  flex-basis: 50%;
 }

.eventAddress {
  font-size: 12px;
}

.going, .visibility {
  font-style: italic;
}

.descriptionStub {
  max-height: 14ex;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-bottom: 1em;
}

.descriptionStub::after {
    content: '';
    position: absolute;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 4em;
    max-height: 30%;
    background-image: linear-gradient(180deg, rgba(255,255,255,0) 10%, white 30%);
    pointer-events: none;
  }


.descriptionStub > :nth-child(n+3) {
  display: none;
} 

.changeNumOfEvents {
  width: 60px;
  margin: 20px 0 50px;
  text-align: center;
}

.Alert {
  height: 40px;
  margin: -15px 0;
}

@media only screen and (min-width:  992px) {
  .App {
    text-align: center;
    padding: 2% 10%;
  }

  .EventList {
    column-count: 2;
   }
}

@media only screen and (min-width: 1200px) { 
  .App {
    text-align: center;
    padding: 2% 15%;
  }
}

